/** Media Queries **/
html {
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Oswald", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #0d1511;
  color: #f0f1f1;
  overflow: hidden;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  body {
    display: flex;
    height: 100vh;
  }
  body aside {
    float: left;
    width: 195px;
    height: 100%;
  }
}

p {
  color: #f0f1f1;
  font-size: 3rem;
}

a {
  text-decoration: none;
  cursor: pointer;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Oswald", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 12.8rem;
  color: #93d5b5;
  text-align: left;
}

h2 {
  font-size: 3.8rem;
  margin-bottom: 0;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.box .navegacion {
  display: flex;
  flex-direction: column;
  position: fixed;
  width: 100%;
  margin-bottom: 2rem;
  background-color: #0d1511;
  z-index: 5;
}
.box .navegacion h4 {
  display: none;
}
.box .navegacion .header {
  display: flex;
  flex-direction: column;
}
.box .navegacion .header .menu {
  cursor: pointer;
  display: flex;
  justify-content: center;
}
.box .navegacion .header .menu .abrir-menu {
  display: block;
}
.box .navegacion .header .menu .cerrar-menu {
  display: none;
}
.box .navegacion .opciones {
  display: none;
}
.box .navegacion .opciones .enlaces {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.box .iconos {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 2rem;
  padding: 0 2rem;
  transition: filter 0.3s ease-in-out;
}
.box .iconos a {
  padding: 0;
}
.box .iconos .linkedin {
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .box {
    margin-right: 2rem;
    margin-top: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    width: 50rem;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .box .navegacion {
    margin-right: 2rem;
    margin-top: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    width: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: fixed;
    z-index: 2;
  }
  .box .navegacion h4 {
    color: #93d5b5;
    display: flex;
    margin-top: 0rem;
    padding-left: 1rem;
    text-align: center;
  }
  .box .navegacion .header .menu {
    display: none;
  }
  .box .navegacion .opciones {
    display: contents;
  }
  .box .navegacion .opciones .enlaces {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    flex: auto;
  }
  .box .iconos {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 2rem;
    padding: 0 2rem;
  }
  .box .iconos a {
    padding: 0;
  }
  .box .iconos .linkedin {
    margin-bottom: 2rem;
  }
}
button {
  all: unset;
}

.boton {
  margin: 1.5rem;
  width: 100%;
  text-align: center;
  color: #f0f1f1;
  padding: 1rem;
  cursor: pointer;
  border: 1px solid #93d5b5;
  border-radius: 2rem;
  transition: filter 0.3s ease-in-out;
}
.boton:hover {
  filter: drop-shadow(0px 0px 30px #18e985);
}

.neon {
  transition: filter 0.3s ease-in-out;
}
.neon:hover {
  filter: drop-shadow(0px 0px 30px #18e985);
  z-index: 5;
}

.secciones {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow-y: scroll;
}
@media (min-width: 1024px) {
  .secciones {
    scroll-snap-type: y mandatory;
    scroll-behavior: smooth;
  }
}
.secciones::-webkit-scrollbar {
  display: none;
}
.secciones section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: normal;
  box-sizing: border-box;
  scroll-snap-align: start;
}
@media (min-width: 1024px) {
  .secciones section {
    height: 100vh;
    margin: 20rem 2rem;
  }
}
.secciones section:first-child, .secciones section:last-child {
  padding-top: 20rem;
}
@media (min-width: 768px) {
  .secciones section:first-child, .secciones section:last-child {
    padding-top: 2rem;
  }
}
.secciones section .titulo {
  margin-top: 4rem;
  margin-bottom: 5rem;
  border-bottom: 1px solid #93d5b5;
}
.secciones section .about {
  display: flex;
  flex-direction: column;
  margin: 0 2rem;
}
@media (min-width: 768px) {
  .secciones section .about {
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding: 20px;
    text-align: left;
  }
}
.secciones section .about .intro {
  display: flex;
  flex-direction: column;
}
.secciones section .about .intereses {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.block {
  display: block;
}

.none {
  display: none;
}

.proyectos {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.proyectos .contenedor {
  margin: 2rem;
  margin-bottom: 6rem;
  width: 80vw;
  height: 30vh;
  background-color: #93d5b5;
  text-align: center;
  justify-self: center;
  align-items: center;
  cursor: pointer;
  transition: all 100s;
  z-index: 3;
}
.proyectos .contenedor p {
  opacity: 0;
}
.proyectos .contenedor p span {
  color: #18e985;
}
.proyectos .contenedor:hover {
  background-color: white;
  background-image: none;
  z-index: 1;
}
.proyectos .contenedor:hover p {
  opacity: 1;
  color: black;
}

@media (min-width: 768px) {
  .proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .proyectos .contenedor {
    min-width: 300px;
    max-width: 300px;
    margin: 2rem auto; /* Centra el div horizontalmente */
    background-color: #93d5b5;
    text-align: center;
    justify-self: center;
    align-items: center;
    transition: background-color 500ms;
    cursor: pointer;
  }
}
@media (min-width: 1024px) {
  .proyectos {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .proyectos .contenedor {
    min-width: 400px;
    max-width: 600px;
    margin: 0 auto; /* Centra el div horizontalmente */
    background-color: #93d5b5;
    text-align: center;
    justify-self: center;
    align-items: center;
    cursor: pointer;
  }
}
.bienes {
  background-image: url(/build/img/proyectos/bienes/1.JPG);
  background-size: cover;
}

.salon {
  background-image: url(/build/img/proyectos/salon/1.JPG);
  background-size: cover;
}

.capacidades {
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin-bottom: 2rem;
}
.capacidades .tecnologias {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .capacidades .tecnologias {
    display: flex;
    flex-direction: column;
  }
}
.capacidades .tecnologias .grid-tecnologias {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}

@media (min-width: 768px) {
  .capacidades {
    display: flex;
    flex-direction: column;
    flex: auto;
    margin-bottom: 2rem;
  }
  .capacidades .tecnologias {
    display: flex;
    flex-direction: column;
  }
  .capacidades .tecnologias .grid-tecnologias {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .capacidades {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    flex: auto;
    margin-bottom: 0;
  }
  .capacidades .tecnologias {
    display: flex;
    flex-direction: column;
  }
  .capacidades .tecnologias .grid-tecnologias {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.idiomas {
  display: flex;
  flex-direction: column;
  align-content: center;
  text-align: center;
}
.idiomas .contenedor-idiomas {
  display: flex;
  padding: 2rem;
  background-color: white;
  border: 1px solid #93d5b5;
  color: black;
}
.idiomas .contenedor-idiomas span {
  text-transform: uppercase;
  color: #18e985;
}

.contenedor-tecnologias {
  background-color: #FFFFFF;
  height: 200px;
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease-out;
}
.contenedor-tecnologias img {
  width: 90%;
  background-color: transparent;
}
.contenedor-tecnologias p {
  display: none;
}
.contenedor-tecnologias:hover {
  display: flex;
  flex-direction: column;
  background-color: rgba(197, 196, 196, 0.431372549);
  z-index: 1;
}
.contenedor-tecnologias:hover img {
  display: none;
}
.contenedor-tecnologias:hover p {
  display: flex;
  text-align: center;
  justify-content: center;
  opacity: 1;
}

.star {
  position: absolute;
  width: 10px;
  height: 10px;
  background: radial-gradient(circle, transparent 1%, #93d5b5 100%);
  animation: twinkle 2s linear infinite;
  z-index: 2;
}

@keyframes twinkle {
  0% {
    opacity: 0;
    transform: translateY(0);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(100%);
  }
}
.modal {
  display: none; /* Oculta la modal por defecto */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 4;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.74);
}
@media (min-width: 768px) {
  .modal {
    position: absolute;
  }
}
.modal .imagenes {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-y: scroll;
}
.modal button {
  cursor: pointer;
}

.modal-content {
  background-color: #fefefe;
  padding: 20px;
  border: 1px solid #888;
  width: 100%;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .modal-content {
    max-width: 60%;
  }
}

.close {
  color: #aaa;
  float: right;
  font-size: 70px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #dcf748;
  text-decoration: none;
  cursor: pointer;
}

@media (min-width: 1024px) {
  .mtop {
    margin-top: 200rem;
  }
}